<template>
  <transition name="slide">
    <div class="seach-main">
      <div class="search-box">
        <div class="box">
          <div class="icon-search">
            <i class="iconfont icon-sousuo"></i>
          </div>
          <input ref="query" v-model="query" class="box-input" :placeholder="placeholder" />
          <van-icon style="font-size:12px;" @click="clear" v-show="query" name="close" />
        </div>
        <div class="cancel" @click="cancel">
          取消
        </div>
      </div>
      <div class="search-content" ref="search" v-show="query && query.length">
        <div>
          <div class="item" v-for="(val,index) in seachList" :key="index" @click="addHospital(val)">
            <div class="hospital-name">
              {{ val.hospitalName }}
            </div>
            <div class="city-types">
              {{ val.provinceName }}{{ val.cityName }}{{ val.countyName }} | {{ val.levelName }}
            </div>
          </div>
        </div>
      </div>
      <div class="seachResult" v-show="query && query.length && noResult">
        抱歉，暂无搜索结果
      </div>
    </div>
  </transition>
</template>

<script>
const LIMIT = 20
import BScroll from 'better-scroll'
import { debounce } from '@/js/util'
export default {
  data() {
    return {
      placeholder: '搜索...',
      query: '',
      seachList: [],
      scroll: '',
      noResult: false
    }
  },
  created() {
    this.$watch('query', debounce(this._seach, 200))
  },
  mounted() {
    setTimeout(() => {
      this.init()
    }, 20)
  },
  methods: {
    _seach(newQuery) {
      if(!newQuery) {
        return
      }
      this.$http.get('/api/support/findHospitalListByName', {params: {name: newQuery, limit: LIMIT}}).then(res => {
        if (!res.data.success) {
          return
        }
        console.log(res)
        this.seachList = res.data.data
        this.noResult = this.seachList.length === 0
      }).catch((err) => {
        console.log(err)
      })
    },
    init() {
      this.scroll = new BScroll(this.$refs.search, {
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
      this.blur()
    },
    refresh() {
      this.scroll && this.scroll.refresh()
    },
    clear() {
      this.query = ''
    },
    cancel() {
      this.$router.back()
    },
    setQuery(query) {
      this.query = query
    },
    blur() {
      this.$refs.query.blur()
    },
    addHospital(_data) {
      this.$store.dispatch( this.$route.params.id, _data );
      this.cancel()
    }
  },
  watch: {
    seachList() {
      setTimeout(() => {
        this.refresh()
      }, 20)
    }
  }
}
</script>


<style lang="scss" scoped>
@import '~@/css/mixin.scss';
.seach-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index:1000;
  background: $background-color;
  .search-box{
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    height: 44px;
    background: #fff;
    align-items: center;
    padding: 0  0 0 15px;
    // background-image: linear-gradient(to right, $color-theme, ($color-gradient-end));
    .box {
      flex: 1;
      background:rgb(235,235,235);
      border-radius: 6px;
      display: flex;
      align-items: center;
      padding-right: 5px;
      .icon-search {
        flex: 26px 0 0;
        text-align: center;
      }
      .box-input {
        flex: 1;
        background: none;
        font-size: 14px;
        outline: 0;
        line-height: 18px;
        padding: 5px 0;
      }
    }
    .cancel{
      flex: 60px 0 0;
      text-align: center;
      line-height: 44px;
      height: 44px;
      color: $color-theme;
    }
  }
  .search-content {
    position: absolute;
    top: 55px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    .item {
      display: flex;
      background: #fff;
      font-size: 14px;
      height: 50px;
      line-height: 20px;
      padding: 0 20px;
      @include border-bottom-1px();
      &:last-child::after{
        display:none
      }
      .hospital-name{
        display: flex;
        align-items: center;
        flex: 1;
      }
      .city-types {
        display: flex;
        align-items: center;
        flex: 1;
        text-align: right;
      }
    }
  }
  .seachResult {
    text-align: center;
    font-size: 14px;
    margin-top: 30px;
    color: rgba(135, 135, 135, 0.3);
  }
}
</style>
